.btn {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1.2rem;
  padding: .7rem 1rem .7rem 1rem;

  transition: color .3s ease;

  &:focus {
    outline: none;
  }

  &:hover {
    color: darken($color: white, $amount: 10);
  }

  &--block {
    width: 100%;
  }

  &--primary {
    background-color: $primary;
    border-radius: .2rem;

    &:disabled {
      background-color: lighten($color: $primary, $amount: 15);
    }
      
    &:hover {
      color: darken($color: $primary, $amount: 25);
    }
  }

  &--danger {
    background-color: red;

    transition: background-color .3s ease;

    &:hover {
      background-color: darken($color: red, $amount: 10);
    }
  }
}

.google-btn {
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 42px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white;
    }
    .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        text-align: center;
        margin: 11px 0 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}
